import React from 'react';
import { Button, Container, Table, Row, Col } from 'react-bootstrap';
import { useDispatch, useSelector } from "react-redux";
import { addCount, sortName, decreaseCount } from "./../store.js";
import { useTranslation } from 'react-i18next';

function Cart() {
   const user = useSelector(state => state.user);
   const cart = useSelector(state => state.cart);
   const { name, age } = user;
   const dispatch = useDispatch();

   //   
   const total = cart.reduce((acc, item) => acc + item.price * item.amount, 0);
const { t } = useTranslation();
   return (
      <Container style={{ paddingTop: '100px', paddingBottom: '10%' }} className='Cart'>
         <h5>{name} {age} () </h5>

         <Table>
            <thead>
               <tr>
                  <th className='Cartremove'></th>
                  <th className='Cartremove'></th>
                  <th className='Cartremove'> </th>
                  <th className='Cartremove1'></th>
                  <th className='Cartremove'></th>
               </tr>
            </thead>
            <tbody>
               {
                  cart.map((v, i) => (
                     <tr key={i}>
                        <td className="tdimg">
                           <Row className="align-items-center">
                              <Col xs={12} className="text-center mb-2">
                                 <img
                                    src={process.env.PUBLIC_URL + v.imgUrl}
                                    alt={t(v.item)}
                                    style={{ width: '100px' }}
                                 />
                              </Col>
                              <Col xs={12} className="text-center">
                                {t(v.item)}
                              </Col>
                           </Row>
                        </td>
                        <td>{v.amount}</td>
                        <td>{v.price.toLocaleString()}</td>
                        <td className='Cartremove1'>{(v.price * v.amount).toLocaleString()}</td>
                        <td className='amoutBTN'>
                           <button onClick={() => dispatch(addCount(i))}>
                              <span className="material-symbols-outlined">add</span>
                           </button>
                           <button onClick={() => dispatch(decreaseCount(i))}>
                              <span className="material-symbols-outlined">remove</span>
                           </button>
                        </td>
                     </tr>
                  ))
               }
            </tbody>
         </Table>

         <div style={{ textAlign: 'right', marginTop: '20px' }}>
            <h4> : {total.toLocaleString()}</h4>
           <button onClick={() => alert(' ')}></button>
         </div>
      </Container>
   );
}

export default Cart;
